html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
body.body-scroll {
  padding-top: 8.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
  margin: 2rem;
  line-height: 1.5;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  display: flex;
  justify-content: space-around;
  position: fixed;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.3rem rgba(0, 0, 0, 0.5);
  z-index: 100;
  top: 0;
  left: 0;
  background-color: rgba(60, 119, 214, 0.699);
}
.header h1 {
  color: #000000;
  text-align: center;
  margin: 0;
  padding: 2rem 0;
}
.header h1:hover {
  transition: 0.5s;
  color: #0D6EFD;
}
.header .abrir-menu,
.header .cerrar-menu {
  display: none;
}
@media (max-width: 1050px) {
  .header .abrir-menu,
  .header .cerrar-menu {
    display: block;
    border: 0;
    background-color: transparent;
    color: #000000;
    font-size: 3rem;
    cursor: pointer;
  }
  .header .navegacion-principal {
    opacity: 0;
    visibility: hidden;
    display: flex;
    align-items: end;
    gap: 1rem;
    flex-direction: column;
    position: fixed;
    color: #FFFFFF;
    align-items: start;
    background-color: #333;
    padding: 2rem;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
    height: 100vh;
    width: 100%;
    transform: translateX(100%);
  }
  .header .navegacion-principal a {
    color: #FFFFFF;
  }
  .header .navegacion-principal button {
    color: #FFFFFF;
  }
  .header .navegacion-principal i {
    color: #FFFFFF;
  }
  .header .nav-visible {
    opacity: 1;
    visibility: visible;
    transition: 0.5s;
    transform: translateX(0);
  }
}

.abrir-menu {
  display: none;
}

.navegacion-principal .nav__link__active {
  border-bottom: 2px solid #0D6EFD;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-principal button {
  border: 0;
  background-color: transparent;
  color: #000000;
  font-size: 1.5rem;
  cursor: pointer;
  font-weight: 500;
}
.navegacion-principal button:hover {
  border-bottom: 2px solid #0D6EFD;
}
.navegacion-principal a {
  transition: 0.5s;
  display: block;
  text-align: center;
  color: #000000;
  font-size: 2rem;
  margin-bottom: 1rem;
  padding: 0.5rem 0;
  font-weight: 500;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  border-bottom: 2px solid #0D6EFD;
}

.switch {
  display: flex;
  width: 60px;
  height: 30px;
  justify-content: space-between;
  padding: 0 5px;
  align-items: center;
  position: relative;
  border-radius: 100px;
  border: #333 2px solid;
  background-color: #132b41;
}
.switch i {
  font-size: 1.3rem;
  color: #fdc21c;
}

.switch:last-child::before {
  content: "";
  position: absolute;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background-color: #FFFFFF;
  border: solid thin #000000;
  left: 0;
  right: unset;
}

.switch:last-child.active::before {
  right: 0;
  left: unset;
  border: solid thin #FFFFFF;
}

.switch:last-child.active i:first-child {
  color: #FFFFFF;
}

.switch:last-child.active {
  background-color: #fdc21c;
  border: solid 2px #FFFFFF;
}

.idioma-toggle {
  background-color: transparent;
  border: 2px solid #0D6EFD;
  color: #000000;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 6px 12px;
  border-radius: 25px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
}
.idioma-toggle img {
  width: 24px;
  height: 24px;
}
.idioma-toggle i {
  font-size: 1rem;
  color: #0D6EFD;
}
.idioma-toggle:hover {
  background-color: #0D6EFD;
  color: white;
}
.idioma-toggle:hover i {
  color: white;
}

.inicio {
  display: flex;
  gap: 3rem;
  justify-content: space-around;
  width: 100%;
  height: 35vh;
  margin-top: 20vh;
}
.inicio h1,
.inicio i {
  transition: 0.5s;
}
.inicio .title-color {
  color: #0D6EFD;
}
.inicio .button {
  transition: 0.5s;
  display: inline-block;
  background-color: #0D6EFD;
  color: #fff;
  padding: 0.75rem 2.5rem;
  font-weight: 600;
  border-radius: 0.5rem;
}
.inicio .button:hover {
  box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
}
.inicio .home__social {
  display: flex;
  flex-direction: row;
}
.inicio .home__social a {
  width: max-content;
  font-size: 5.5rem;
  color: #000000;
  margin: 5rem 3rem;
  transition: 0.5s;
}
@media (max-width: 768px) {
  .inicio .home__social a {
    font-size: 4rem;
  }
}
.inicio i:hover {
  cursor: pointer;
  transition: 0.2s;
  color: #0D6EFD;
}

.first-part {
  display: flex;
  align-items: start;
  flex-direction: column;
  width: 50%;
}

.second-part {
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 500px; /* Agrega profundidad */
  display: flex;
  justify-content: center;
  align-items: center;
}

.imagen1 {
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 25px 25px rgba(14, 36, 49, 0.35);
  width: 35vh;
  height: 35vh;
  transition: transform 0.2s ease-out;
}

.second-part:hover .imagen1 {
  transition: none; /* Elimina la transición para movimientos en vivo */
}

.sobre-mi-content {
  display: flex;
  align-items: center;
  justify-content: center;
}
.sobre-mi-content .sobre-mi__contenedor2 {
  transition: 0.5s;
  width: 50%;
  text-align: center;
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
  padding: 1.1rem 0;
  border-radius: 0.5rem;
}
.sobre-mi-content .sobre-mi__contenedor2 p {
  transition: 0.5s;
}

.sobre-mi__contenedor_title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10rem;
}
.sobre-mi__contenedor_title .sobre-mi__title {
  color: #0D6EFD;
  font-size: 3.3rem;
  font-weight: 500;
}
.sobre-mi__contenedor_title ::after {
  content: "";
  display: block;
  width: 50%;
  height: 3px;
  background-color: #0D6EFD;
  margin: auto;
}

.imagen2 {
  border-radius: 50%;
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
  object-fit: cover;
  margin-right: 5rem;
  transition: transform 0.2s ease-out;
}

.second-part:hover .imagen2 {
  transition: none; /* Elimina la transición para movimientos en vivo */
}

.proyects {
  display: flex;
  flex-direction: column;
  margin-top: 15rem;
}

.proyects-web,
.proyects-python {
  margin: 10rem;
  display: grid;
  gap: 15rem;
  grid-template-columns: repeat(3, 1fr);
}

.title-principal {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10rem;
}
.title-principal h1 {
  color: #0D6EFD;
  font-size: 3.3rem;
  font-weight: 500;
}
.title-principal ::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background-color: #0D6EFD;
  margin: auto;
}

.title-web,
.title-python {
  display: inline-block;
  margin: 0 auto;
  /* The typewriter cursor effect */
}
.title-web h1,
.title-python h1 {
  transition: 0.5s;
  overflow: hidden;
  border-right: 0.15em solid #0D6EFD; /* Cambia el color y grosor de la línea de escritura */
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.15em; /* Cambia el espaciado entre letras */
  animation: typing 3s infinite alternate, blink-caret 1s infinite step-end; /* Ajusta la duración y otros valores según lo desees */
}
@keyframes typing {
  0% {
    width: 0;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}
@keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: #0D6EFD;
  }
}

.proyecto-1 {
  margin: 5rem;
  text-align: center;
  width: 90%;
  height: 45rem;
  margin: 0 auto;
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
  padding: 1.1rem 0;
  border-radius: 0.5rem;
  overflow: hidden;
  text-align: center;
}
.proyecto-1 .proyecto-title,
.proyecto-1 .proyecto-title-python {
  transition: 0.5s;
  color: #000000;
  font-size: 2rem;
}
.proyecto-1 a {
  transition: 0.5s;
  font-size: 3rem;
  color: #000000;
}
.proyecto-1 .bx {
  transition: 0.5s;
}
.proyecto-1 .bx:hover {
  cursor: pointer;
  transition: 0.5s;
  transform: scale(1.1);
  color: #0D6EFD;
}
.proyecto-1 h4 {
  transition: 0.5s;
}

.imagenweb,
.imagenpython {
  width: 100%;
  height: 70%;
  object-fit: contain;
  transition: 1s;
  cursor: pointer;
}
.imagenweb:hover,
.imagenpython:hover {
  transform: scale(1.05);
}

